"use client";

import React, { useState, useEffect } from "react";
import { X, CheckCircle, AlertTriangle } from "lucide-react";
import { apiRequest } from "@/app/authservice/api";

const PatientUpdateModal = ({ onClose, patient = null, onUpdated }) => {
  const [loading, setLoading] = useState(false);
  const [formData, setFormData] = useState({
    _id: "", // MongoDB ID
    patientId: "",
    name: "",
    phone: "",
    address: "",
    age: "",
    gender: "",
    bloodGroup: "",
  });

  const [formErrors, setFormErrors] = useState({});
  const [modal, setModal] = useState({ show: false, success: true, message: "" });

  // Populate form when patient data changes
  useEffect(() => {
    if (patient) {
      setFormData({
        _id: patient._id || "",
        patientId: patient.patientId || "",
        name: patient.name || "",
        phone: patient.phone || "",
        address: patient.address || "",
        age: patient.age || "",
        gender: patient.gender || "",
        bloodGroup: patient.bloodGroup || "",
      });
    }
  }, [patient]);

  // Handle input changes
  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prev) => ({ ...prev, [name]: value }));
    setFormErrors((prev) => ({ ...prev, [name]: "" }));
  };

  // Handle form submit
  const handleSubmit = async (e) => {
    e.preventDefault();

    // Validation
    const errors = {};
    if (!formData.name) errors.name = "Name is required.";
    if (!formData.phone) errors.phone = "Phone is required.";
    if (!formData.address) errors.address = "Address is required.";
    if (!formData.age) errors.age = "Age is required.";
    if (!formData.gender) errors.gender = "Gender is required.";

    if (Object.keys(errors).length > 0) {
      setFormErrors(errors);
      return;
    }

    // Use FormData
    const payload = new FormData();
    payload.append("patientId", formData.patientId);
    payload.append("name", formData.name);
    payload.append("phone", formData.phone);
    payload.append("address", formData.address);
    payload.append("age", formData.age);
    payload.append("gender", formData.gender);
    payload.append("bloodGroup", formData.bloodGroup);

    try {
      setLoading(true);

      const res = await apiRequest(`/patients/updatePatient/${formData._id}`, {
        method: "PUT",
        body: payload,
        sendingFormData: true, // optional, in case your apiRequest handles FormData
      });

      if (res?.success) {
        setModal({
          show: true,
          success: true,
          message: res.message || "Patient updated successfully",
        });
        onUpdated?.();
      } else {
        setModal({
          show: true,
          success: false,
          message: res?.message || "Failed to update patient",
        });
      }
    } catch (error) {
      console.error("Save patient error:", error);
      setModal({
        show: true,
        success: false,
        message: error.message || "Something went wrong",
      });
    } finally {
      setLoading(false);
    }
  };

  // Close modal
  const closeModal = () => {
    setModal({ show: false, success: true, message: "" });
    if (modal.success) onClose?.();
  };

  return (
    <>
      {/* Main modal */}
      <div className="fixed inset-0 bg-black/50 z-50 flex items-center justify-center p-4">
        <div className="bg-white rounded-2xl shadow-xl w-full max-w-3xl max-h-[90vh] overflow-y-auto">
          {/* Header */}
          <div className="sticky top-0 bg-teal-600 text-white p-6 flex justify-between items-center rounded-t-2xl">
            <h2 className="text-2xl font-bold">Edit Patient</h2>
            <button onClick={onClose} className="p-2 hover:bg-white/20 rounded-lg">
              <X className="w-6 h-6" />
            </button>
          </div>

          {/* Form */}
          <form className="p-6 space-y-6" onSubmit={handleSubmit}>
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div>
                <label className="text-sm font-medium">Patient ID</label>
                <input
                  type="text"
                  name="patientId"
                  value={formData.patientId || ""}
                  readOnly
                  className="w-full h-9 rounded-md border bg-gray-100 px-3"
                />
              </div>

              <div>
                <label className="text-sm font-medium">Full Name *</label>
                <input
                  type="text"
                  name="name"
                  value={formData.name || ""}
                  onChange={handleChange}
                  className="w-full h-9 rounded-md border px-3"
                />
                {formErrors.name && <p className="text-red-600 text-xs mt-1">{formErrors.name}</p>}
              </div>

              <div>
                <label className="text-sm font-medium">Phone *</label>
                <input
                  type="tel"
                  name="phone"
                  value={formData.phone || ""}
                  onChange={handleChange}
                  className="w-full h-9 rounded-md border px-3"
                />
                {formErrors.phone && <p className="text-red-600 text-xs mt-1">{formErrors.phone}</p>}
              </div>

              <div>
                <label className="text-sm font-medium">Address *</label>
                <textarea
                  name="address"
                  rows={2}
                  value={formData.address || ""}
                  onChange={handleChange}
                  className="w-full rounded-md border px-3 py-2"
                />
                {formErrors.address && <p className="text-red-600 text-xs mt-1">{formErrors.address}</p>}
              </div>

              <div>
                <label className="text-sm font-medium">Age *</label>
                <input
                  type="number"
                  name="age"
                  value={formData.age || ""}
                  onChange={handleChange}
                  className="w-full h-9 rounded-md border px-3"
                />
                {formErrors.age && <p className="text-red-600 text-xs mt-1">{formErrors.age}</p>}
              </div>

              <div>
                <label className="text-sm font-medium">Gender *</label>
                <select
                  name="gender"
                  value={formData.gender || ""}
                  onChange={handleChange}
                  className="w-full h-9 rounded-md border px-3"
                >
                  <option value="">Select</option>
                  <option value="Male">Male</option>
                  <option value="Female">Female</option>
                  <option value="Other">Other</option>
                </select>
                {formErrors.gender && <p className="text-red-600 text-xs mt-1">{formErrors.gender}</p>}
              </div>

              <div>
                <label className="text-sm font-medium">Blood Group</label>
                <input
                  type="text"
                  name="bloodGroup"
                  value={formData.bloodGroup || ""}
                  onChange={handleChange}
                  className="w-full h-9 rounded-md border px-3"
                />
              </div>
            </div>

            <div className="flex justify-end gap-3 pt-4">
              <button type="button" onClick={onClose} className="px-4 py-2 border rounded-md">
                Cancel
              </button>
              <button
                type="submit"
                disabled={loading}
                className={`px-4 py-2 rounded-md text-white ${
                  loading ? "bg-gray-400" : "bg-teal-600 hover:bg-teal-700"
                }`}
              >
                {loading ? "Updating..." : "Update Patient"}
              </button>
            </div>
          </form>
        </div>
      </div>

      {/* Success / Error Modal */}
      {modal.show && (
        <div className="fixed inset-0 z-60 flex items-center justify-center bg-black/50">
          <div className="bg-white rounded-xl shadow-lg p-6 w-80 text-center">
            {modal.success ? (
              <CheckCircle className="mx-auto w-12 h-12 text-green-500" />
            ) : (
              <AlertTriangle className="mx-auto w-12 h-12 text-red-500" />
            )}
            <p className="mt-4 text-gray-700">{modal.message}</p>
            <button
              onClick={closeModal}
              className="mt-6 px-4 py-2 bg-teal-600 text-white rounded-md hover:bg-teal-700"
            >
              OK
            </button>
          </div>
        </div>
      )}
    </>
  );
};

export default PatientUpdateModal;
